import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { DecimalPipe } from '@angular/common';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber, decimalNumTwo, checkEnAndNum } from 'app/@uisftech/common/service/validator';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';

@Component({
    selector: 'bigCustomApply',
    templateUrl: './bigCustomApply.component.html',
    styleUrls: ['./bigCustomApply.component.scss'],
    providers: [ConfirmationService, DecimalPipe, BigCusHttpService, CustomeHttpService]
})
export class BigCustomApplyComponent implements OnInit {
    first: number = 0;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    pageSizeOne: number = 10;//
    pageNumOne: number;//
    total_one: string;//总页数
    pageSizeTwo: number = 10;//
    pageNumTwo: number;//
    total_two: string;
    msgs: Message[] = [];
    custNo: string;
    custName: string;
    step_one: boolean = true;
    step_two: boolean = false;
    data_one: any = [];
    data_two: any = [];
    custId: string;//工号
    custIdName: string;//姓名

    feeunitPrice: string;//费用单价
    partyId: string;//工号
    lastName: string;//工号名称
    divideRate: any;//分成比例
    relDepOrgList: string;//

    userform: FormGroup;
    moneyform: FormGroup;
    showUpdate: boolean = true;

    bigCustNo: string;
    bigId: string;

    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    depositSumTypeOptions: any = [];//存款累计方式
    code: any;
    manageId: string;
    sumshow: boolean = false;

    submitNum: string;

    stop_flag: boolean = true;//调整进来不可以操作
    //选人
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;
    saveFeeMon: string;//暂存的费用单价
    isShowTip: boolean = true;//校验隐藏
    money_flag: number = 0;//修改费用单价的标志
    on_cust: string;//暂存的客户号
    on_userID: string;//暂存的协储员号
    addFlagBtn: boolean = false;//名单信息添加开关
    addMoeyFlag: boolean = false;//费用信息添加开关
    constructor(
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: BigCusHttpService,
        public decimalPipe: DecimalPipe,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public routeInfo: ActivatedRoute,
        public cusOperationHttpService: CusOperationHttpService,
        public customeHttpService: CustomeHttpService
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }

    }

    ngOnInit() {
        this.codeValues();
        this.depositSumTypeOptions = this.code['depositSumTypeBigCus'];//存款累计方式

        this.pageNumOne = 1;
        this.pageNumTwo = 1;
        this.pageSizeOne = 10;
        this.pageSizeTwo = 10;
        this.userform = this.fb.group({
            'custNo': new FormControl('', Validators.required),
            'custName': new FormControl('', Validators.required),
        });
        this.moneyform = this.fb.group({
            'feeunitPrice': new FormControl('', Validators.required),
            'custId': new FormControl('', Validators.required),
            'custIdName': new FormControl('', Validators.required),
            'divideRate': new FormControl('')
        });
        console.log(this.routeInfo.snapshot.params)
        if (this.routeInfo.snapshot.params['this_Flag']) {
            this.bigCustNo = this.routeInfo.snapshot.params['bigCustNo'];
            this.bigId = this.routeInfo.snapshot.params['bigId'];
            this.queryBigCus();
            this.queryMoney();
            this.queryBase();//查询费用单价
        } else {
            this.data_one = [];
            this.data_two = [];
        }
        if (this.routeInfo.snapshot.params['this_Flag'] == '1') {
            this.stop_flag = false;
        } else {
            this.stop_flag = true;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //校验
    checkPerson() {
        if (this.custNo && this.custNo != '') {
            this.httpService.findUser({ custNo: this.custNo }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.infoList != null) {
                        if (data.infoList.length > 0) {
                            this.manageId = data.infoList[0].manageId;
                            if (this.manageId == this.userId) {
                                this.showName();
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: '当前登陆人不是该客户的主联系人,请重新输入!' });
                            }

                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '没有可选的主联系人!' });
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '没有可选的主联系人!' });
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }
    //回显客户名称
    showName() {
        this.isShowTip = true;
        if (this.custNo && this.custNo != '') {
            let param = {
                custNo: this.custNo
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.on_cust = JSON.parse(JSON.stringify(this.custNo));
                    if (data.dataSourceId == 'BR00B' && this.user.targetId == '101011') {
                        this.custName = data.custName;
                    } else {
                        if (data.dataSourceId == 'BR00A' && this.user.targetId == '101010') {
                            this.custName = data.custName;
                        } else {
                            this.isShowTip = false;
                            this.reset(1);
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前非台行客户!' }];
                            return
                        }
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //校验客户号只能输入数字
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (num == 1) {
                if (checkNumber(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                    return
                }
                if (custNoTen(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                    return
                }
            } else if (num == 2) {
                if (checkEnAndNum(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '协储员号错误!' }];
                    return
                }
            }
        } else {
            if (num == 2) {
                this.custIdName = '';
            } else if (num == 1) {
                this.custName = '';
            }
        }
    }
    //校验工号
    checkNum() {
        if (this.custId && this.custId != '') {
            this.httpService.queryManagerInfo({ assiterId: this.custId }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.userId && data.userId != '') {
                        this.queryUserId(data.userId);
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    queryUserId(item) {
        this.httpService.queryPersonRelListByPartyId({ partyIdList: [item] }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.on_userID = JSON.parse(JSON.stringify(this.custId));
                if (data.relList.length == 0 || data.relList == null) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "工号错误,请重新输入!" });
                    this.custId = '';
                    this.custIdName = '';
                    return;
                }
                if (data.relList[0].statusId != "0") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "当前客户经理状态有误,请重新输入!" });
                    this.custId = '';
                    this.custIdName = '';
                    return;
                }
                this.custIdName = data.relList[0].lastName;
                this.partyId = data.relList[0].partyId;
                this.lastName = data.relList[0].lastName;
                this.relDepOrgList = data.relList[0].relDepOrgList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //上下一步
    toNext(data) {
        if (data == 1) {
            if (this.data_one.length == 0 || (this.data_one.length == 1 && this.data_one[0].custNo == '' && this.data_one[0].custName == '')
            ) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "请先录入大客户名单信息!" });
                return;
            }
            this.step_one = false;
            this.step_two = true;
        } else if (data == 2) {
            this.step_one = true;
            this.step_two = false;
        }
    }

    //查询大客户客户信息
    queryBigCus() {
        let items = {
            bigCustNo: this.bigCustNo,
            operationType: '0',
            pageSize: this.pageSizeOne,
            pageNum: this.pageNumOne,
            userName: this.userName
        }
        this.httpService.queryBigCustInfo(items).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.bigCustInfoNewList && data.bigCustInfoNewList.length > 0) {
                    this.data_one = data.bigCustInfoNewList;
                    let sum1 = 0;
                    let sum2 = 0;
                    let sum3 = 0;
                    let sum4 = 0;
                    for (let item of this.data_one) {
                        sum1 += item.byrjckSum;
                        sum2 += item.byrjdkjckSum;
                        sum3 += item.snrjckSum;
                        sum4 += item.snrjdkjckSum;
                    }
                    let temp = {
                        "custNo": "",
                        "custName": "",
                        "byrjckSum": sum1,
                        "byrjdkjckSum": sum2,
                        "snrjckSum": sum3,
                        "snrjdkjckSum": sum4,
                        "relatedCustName": "",
                        "depositSumType": "",
                    }
                    this.data_one.push(temp)
                    for (let param of this.data_one) {
                        if (param.custNo == "" &&
                            param.custName == "" &&
                            param.relatedCustName == "" &&
                            param.depositSumType == ""
                        ) {
                            param.sumshow = true;
                        }
                    }
                } else {
                    this.data_one = [];
                }
                this.total_one = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //翻页
    paginateOne(event) {
        //每页显示的条数
        this.pageSizeOne = event.rows * 1;
        //当前页
        this.pageNumOne = event.page + 1;
        this.first = event.page * this.pageSizeOne;
        this.queryBigCus();
    }
    //新增客户信息
    add() {
        this.addFlagBtn = true;
        this.isShowTip = true;
        if (!this.custName || this.custName == '') {
            this.showName();
        }
        if (this.custNo != this.on_cust) {
            this.showName();
        }
        if (this.routeInfo.snapshot.params['this_Flag'] == '1') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前大客户信息不可以新增!' }];
            return
        }
        setTimeout(() => {
            //校验的
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            let params = {
                userName: this.userName,
                custNo: this.custNo,
                custName: this.custName,
                operationType: '0',
                bigCustNo: this.bigCustNo,
            }
            this.httpService.addBigCustInfo(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.addFlagBtn = false;
                    this.isShowTip = false;
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功!' }];
                    this.bigCustNo = data.bigCustNo;
                    if (data.bigId && data.bigId != '' && data.bigId != null) {
                        this.bigId = data.bigId;
                    }
                    this.queryBigCus();
                    this.reset(1);
                } else {
                    this.addFlagBtn = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.addFlagBtn = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }, 500);
    }

    //单个删除
    toDelete(data) {
        if (this.routeInfo.snapshot.params['this_Flag'] == '1') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前大客户信息不可以删除!' }];
            return
        }
        this.confirmationService.confirm({
            message: '是否确认删除?',
            accept: () => {
                let temp = {
                    bigCustId: data.bigId,
                }
                this.httpService.deleteBigCustInfo(temp).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
                        this.queryBigCus();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        });

    }
    //全部删除
    deleteAll() {
        if (this.routeInfo.snapshot.params['this_Flag'] == '1') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前大客户信息不可以删除!' }];
            return
        }
        if (this.data_one.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '当前表格没有数据,无法进行删除!' }];
            return
        }
        this.confirmationService.confirm({
            message: '是否确认全部删除?',
            accept: () => {
                let temp = {
                    bigCustNo: this.data_one[0].bigCustNo,
                }
                this.httpService.deleteBatchBigCustInfo(temp).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
                        this.queryBigCus();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        });

    }
    //重置
    reset(data) {
        if (data == 1) {
            this.custNo = '';
            this.custName = '';
        } else if (data == 2) {
            this.custId = '';
            this.custIdName = '';
            this.divideRate = '';
        }
    }
    //新增大客户费用信息
    addMoney() {
        this.addMoeyFlag = true;
        this.isShowTip = true;
        if (this.custId != this.on_userID) {
            this.checkNum();
        }
        //校验的
        for (const i in this.moneyform.controls) {
            this.moneyform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.moneyform.controls) {
            if (!this.moneyform.controls[i].valid) {
                return;
            }
        }
        if (Number(this.divideRate) == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例不得为0!' }];
            return
        }
        if (this.data_two.length == 5) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '最多录入5个客户经理!' }];
            return
        }
        if (Number(this.divideRate) > 100) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例不能大于100%!' }];
            return
        }
        if (this.data_two.length >= 1) {
            let sum = Number(this.divideRate);
            for (let item of this.data_two) {
                sum += Number(item.divideRate);
            }
            if (sum > 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例合计之和必须为100%!' }];
                return
            }
        }
        if (this.data_two.length == 4) {
            let sum1 = Number(this.divideRate);
            for (let item of this.data_two) {
                sum1 += Number(item.divideRate);
            }
            if (sum1 > 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例合计之和必须为100%!' }];
                return
            }
        }
        let itemParam;
        if (this.divideRate != '' && this.divideRate && this.divideRate != undefined) {
            itemParam = JSON.parse(JSON.stringify(this.divideRate));
        }
        let params = {
            bigCustNo: this.bigCustNo,
            relDepOrgList: this.relDepOrgList,
            orgId: this.orgId,
            orgName: this.orgName,
            feeunitPrice: this.feeunitPrice,
            divideRate: (itemParam / 100).toString(),
            assitUserId: this.custId,
            assitUserName: this.custIdName
        }
        this.httpService.addBigCustManager(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addMoeyFlag = false;
                this.isShowTip = false;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功!' }];
                this.queryMoney();
                this.reset(2);
                this.saveFeeMon = JSON.parse(JSON.stringify(this.feeunitPrice));
            } else {
                this.addMoeyFlag = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.addMoeyFlag = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    toUpdate(data) {
        data.showUpdate = true;
    }
    //修改保存
    toUpdateSave(item) {
        let itemParam = JSON.parse(JSON.stringify(item))
        let param = {
            bigCustId: item.bigId,
            divideRate: (itemParam.divideRate / 100).toString(),
        }
        this.httpService.updateBigCustManager(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                item.showUpdate = false;
                this.queryMoney();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //查询大客户分成信息
    queryMoney() {
        let params = {
            bigCustNo: this.bigCustNo,
            pageSize: this.pageSizeTwo,
            pageNum: this.pageNumTwo,
            isHis: '1'
        }
        this.httpService.queryBigCustManager(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.bigCustManNewList && data.bigCustManNewList.length > 0) {
                    for (let item of data.bigCustManNewList) {
                        item.divideRate = JSON.parse(JSON.stringify(item.divideRate * 100)).toFixed(2);
                    }
                    this.data_two = data.bigCustManNewList;
                } else {
                    this.data_two = [];
                }
                this.total_two = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //基本信息查询
    queryBase() {
        let param = {
            bigCustNo: this.bigCustNo,
            isHis: '1',
            bigId: this.bigId
        }
        this.httpService.queryBigCustBaseInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.feeunitPrice = data.feeunitPrice;
                this.saveFeeMon = JSON.parse(JSON.stringify(this.feeunitPrice));
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //翻页
    paginateTwo(event) {
        //每页显示的条数
        this.pageSizeTwo = event.rows * 1;
        //当前页
        this.pageNumTwo = event.page + 1;
        this.first = event.page * this.pageSizeTwo;
        this.queryMoney();
    }
    //删除
    toDeleteMoney(data) {
        this.confirmationService.confirm({
            message: '是否确人删除?',
            accept: () => {
                let params = {
                    isAll: '1',
                    bigCustNo: data.bigCustNo,
                    bigCustId: data.bigId
                }
                this.httpService.deleteBigCustManager(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
                        this.queryMoney();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        });
    }
    //全部删除费用信息
    deleteALLMoney() {
        if (this.data_two.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '当前表格没有数据,无法进行删除!' }];
            return
        }
        this.confirmationService.confirm({
            message: '是否确认全部删除?',
            accept: () => {
                let params = {
                    isAll: '0',
                    bigCustNo: this.data_two[0].bigCustNo,
                    bigCustId: this.data_two[0].bigId
                }
                this.httpService.deleteBigCustManager(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '全部删除成功!' }];
                        this.queryMoney();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        });
    }
    //提交
    commit() {
        if (this.data_two.length == 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请先录入客户经理信息!" });
            return;
        }
        if (this.data_two.length == 1) {
            let sum = 0;
            for (let item of this.data_two) {
                sum += Number(item.divideRate);
            }
            if (sum != 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例合计必须为100%!请修改!' }];
                return
            }
        }
        if (this.data_two.length > 1) {
            let sum = 0;
            for (let item of this.data_two) {
                sum += Number(item.divideRate);
            }
            if (sum != 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例合计之和必须为100%!' }];
                return
            }
        }
        this.confirmationService.confirm({
            message: '是否确认提交?',
            accept: () => {
                // setTimeout(() => this.toSubmit(), 100);
                this.submitNum = '1';
                this.submit_1();
            }
        });
    }
    // //提交审批
    // toSubmit() {
    //     this.confirmationService.confirm({
    //         message: '是否已享受差异化?',
    //         accept: () => {
    //             this.submitNum = '0';
    //             this.submit_1();
    //         },
    //         reject: () => {
    //             this.submitNum = '1';
    //             this.submit_1();
    //         }
    //     });

    // }
    submit_1() {
        if (this.money_flag != 1) {
            this.changeAgain();
        }
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.waitSelUserList = this.commfunc.query('DKHFYSQ', {});
        if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let chooseThis = this.waitSelUserList.display;
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let a2 = commonHeader.extParam.specifyNextOpers;
            if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                this.downloadPerson = true;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '没有审批人无法提交!' }];
                return;
                // this.submit_2();
            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }
    }
    //再次修改费用单价
    changeAgain() {
        if (this.feeunitPrice != '' && this.feeunitPrice && (this.feeunitPrice != this.saveFeeMon)) {
            console.log('再次修改费用单价')
            let params = {
                bigCustNo: this.bigCustNo,
                relDepOrgList: [],
                orgId: '',
                orgName: '',
                feeunitPrice: this.feeunitPrice,
                divideRate: '',
                assitUserId: '',
                assitUserName: '',
                bigId: this.bigId,
            }
            this.httpService.addBigCustManager(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.money_flag = 1;
                    this.saveFeeMon = JSON.parse(JSON.stringify(this.feeunitPrice));
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //提交审批
    submit_2() {
        let param = {
            bigCustNo: this.bigCustNo,
            bigId: this.bigId,
            isDiff: this.submitNum
        }
        this.httpService.approveBigCustApplication(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                setTimeout(() => {
                    this.router.navigate(["pages/tzb/custom/bigCustomer/big-cus-apply/bigCustomHistoryQuery"])
                }, 1000);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //保留两位小数
    checkNumtwo(event) {
        if (event.target.value != '') {
            if (decimalNumTwo(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '只能输入数字,输入格式请保留两位小数!' }];
                event.target.value = '';
                return
            }
        }
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.submit_2();
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //绑定权限id
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
